	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early morning, the film \"Reincarnation\" on TV was an exploration video. The previous paragraph seems to have some criticism and is therefore of interest. It is a fictional story that probably won't happen in real life. However, it reflects a new cultural mentality of the society, that is, some people are stunned in the process of social changes, and they do not know the taste of life. At the same time, it emphasizes a feeling that life and love are in the feeling, and feeling good is life, so there is a dancer who will feel good about an idle person. There is probably in real life, but it will not be a common phenomenon. Today, under the culture of the commodity economy, there are fewer and fewer people talking about feelings, and more and more people are talking about profit. Sometimes people don't know what to talk about and what others talk about. There are always people who are just right, but who is it?</p>","id":"/home/swain/workspace/political-life/src/pages/en/0069.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0069.md","frontmatter":{"title":"Wednesday, April 6"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0069.md absPath of file >>> MarkdownRemark"}}

